(function() {
    const horas = document.querySelectorAll('#horas');

    if(horas) {
        const dias = document.querySelectorAll("[name='dia']");
        const inputHiddenDia = document.querySelector("[name='dia_id']");
        const inputHiddenHora = document.querySelector("[name='hora_id']");

        const categoria = document.querySelector("[name='categoria_id']");

        let busqueda = {
            categoria_id: categoria.value || '',
            dia: inputHiddenDia.value || ''
        }

        if(!Object.values(busqueda).includes('')) {
            
            (async () => {
                await buscarEvento();

                const idHora = inputHiddenHora.value;
                const horaSeleccionada = document.querySelector(`[data-hora-id='${idHora}']`);
                horaSeleccionada.classList.remove('horas__hora--deshabilitado');
                horaSeleccionada.classList.add('horas__hora--seleccionada');
            })();
        } 


        dias.forEach(dia => {
            dia.addEventListener('change', terminoBusqueda);
        });

        categoria.addEventListener('change', terminoBusqueda);


        function terminoBusqueda(e) {
            
            busqueda[e.target.name] = e.target.value;

            //reseteamos el valor de hora
            inputHiddenHora.value = '';
            inputHiddenDia.value = '';

            if(Object.values(busqueda).includes('')) {
                return;
            }

            buscarEvento();
        }

        async function buscarEvento() {

            const {categoria_id,dia} = busqueda;
            const url = `/api/eventos?dia_id=${dia}&categoria_id=${categoria_id}`;

            const resultado = await fetch(url);
            const eventos = await resultado.json();

            obtenerHoraDisponible(eventos);
        }

        function obtenerHoraDisponible(eventos) {

            //RESETEAMOS LA CLASE SELECTOR DESHABILITADO
            const listadoHoras = document.querySelectorAll("#horas li");
            listadoHoras.forEach(li => li.classList.add('horas__hora--deshabilitado'));
            listadoHoras.forEach(li => li.classList.remove('horas__hora--seleccionada'));

            //Revisar los eventos
            const horasTomadas = eventos.map(evento => evento.hora_id );

            const listadoHorasArray = Array.from(listadoHoras);
            const resultado = listadoHorasArray.filter(li => !horasTomadas.includes(li.dataset.horaId));

            resultado.forEach(li => li.classList.remove('horas__hora--deshabilitado'));

            

            const horasDisponibles = document.querySelectorAll("#horas li:not(.horas__hora--deshabilitado)");

            horasDisponibles.forEach(hora => hora.addEventListener('click',seleccionarHora));
        }

        function seleccionarHora(e) {
            //Eliminar horaSeleccionadaPrevia
            const horaPreviaSeleccionada = document.querySelector(".horas__hora--seleccionada");

            if(horaPreviaSeleccionada) {
                horaPreviaSeleccionada.classList.remove('horas__hora--seleccionada');
            }

            //AÃ±adimos clase seleccionada
            e.target.classList.add("horas__hora--seleccionada");
            inputHiddenHora.value = e.target.dataset.horaId;

            //AÃ±adimos el valor de dia al input hidden
            inputHiddenDia.value = document.querySelector("[name='dia']:checked").value;
        }
    }


})();