(function() {
    
    const ponentesInput = document.querySelector("#ponentes");

    if(ponentesInput) {
        let ponentes = [];
        let ponentesFiltrados = [];

        const listadoPonentes = document.querySelector("#listado-ponentes");
        const ponenteHidden = document.querySelector("[name='ponente_id']");

        obtenerPonentes();

        if(ponenteHidden.value) {
            (async () => {
                const idPonente = ponenteHidden.value;
                const ponente = await obtenerPonente(idPonente);  
                const {nombre,apellido} = ponente;

                const ponenteHTML = document.createElement('LI');
                ponenteHTML.classList.add("listado-ponentes__ponente--seleccionado");
                ponenteHTML.textContent = `${nombre} ${apellido}`;

                listadoPonentes.appendChild(ponenteHTML);
            })();
        }

        async function obtenerPonentes() {
            const url = '/api/ponentes';

            const respuesta = await fetch(url);
            const resultado = await respuesta.json();

            formatearPonentes(resultado);
        }

        async function obtenerPonente(id) {
            const url = `/api/ponente?id=${id}`;

            const respuesta = await fetch(url);
            const resultado = await respuesta.json();

            return resultado;

        }

        function formatearPonentes(arrayPonentes = []) {

            ponentes = arrayPonentes.map(ponente => {
                return {
                    nombre: `${ponente.nombre.trim()} ${ponente.apellido.trim()}`,
                    id: ponente.id
                }
            })

            ponentesInput.addEventListener('input',buscarPonente);
        }

        function buscarPonente(e) {

            ponentesFiltrados = [];

            const busqueda = e.target.value;

            if(busqueda.length > 3) {

                const expresion = new RegExp(busqueda,'i');
                ponentesFiltrados = ponentes.filter(ponente => {
                    if(ponente.nombre.toLowerCase().search(expresion) !== -1) {
                        return ponente;
                    }
                });
            }

            mostrarPonentes();
        }

        function mostrarPonentes() {

            while(listadoPonentes.firstChild) {
                listadoPonentes.removeChild(listadoPonentes.firstChild);
            }

            if(ponentesFiltrados.length > 0) {
                ponentesFiltrados.forEach(ponente => {
                    const ponenteHTML = document.createElement('LI');
                    ponenteHTML.classList.add('listado-ponentes__ponente');
                    ponenteHTML.textContent = ponente.nombre;
                    ponenteHTML.dataset.ponenteId = ponente.id;
                    ponenteHTML.onclick = seleccionarPonente;
    
                    //AÃ±adir al dom
                    listadoPonentes.appendChild(ponenteHTML);
                })
            } else {
                const mensaje = document.createElement('P');
                mensaje.classList.add('listado-ponentes__no-resultado');
                mensaje.textContent = 'No hay resultados para tu bÃºsqueda';

                //AÃ±adir al dom
                listadoPonentes.appendChild(mensaje);

            }
        }

        function seleccionarPonente(e) {

            const ponentePrevio = document.querySelector('.listado-ponentes__ponente--seleccionado');

            if(ponentePrevio) {
                ponentePrevio.classList.remove('listado-ponentes__ponente--seleccionado');
            }

            const ponente = e.target;
            ponente.classList.add("listado-ponentes__ponente--seleccionado");

            ponenteHidden.value = ponente.dataset.ponenteId;
        }
    }
})();